const getRandomAvatar = () => {
  return anonymousAvatars[Math.floor(Math.random() * anonymousAvatars.length)];
};

const getRandomColor = () => {
  const colors = [
    "#667eea",
    "#764ba2",
    "#f472b6",
    "#4ade80",
    "#fbbf24",
    "#ef4444",
    "#06b6d4",
    "#8b5cf6",
  ];
  return colors[Math.floor(Math.random() * colors.length)];
};

// ‚úÖ Testimonial Card Component
const TestimonialCard = memo(
  ({
    testimonial,
    index,
    activeIndex,
    dragX,
    isDragging,
    isAdmin,
    onDelete,
  }) => {
    const isActive = index === activeIndex;
    const offset = index - activeIndex;

    const getTransform = () => {
      let x = offset * 60;
      let rotateY = offset * -8;
      let rotateZ = offset * 2;
      let scale = 1 - Math.abs(offset) * 0.08;
      let z = -Math.abs(offset) * 50;

      if (isActive && isDragging) {
        x += dragX;
        rotateZ += dragX * 0.03;
        rotateY += dragX * 0.05;
      }

      return `
      perspective(1200px)
      translateX(${x}px)
      translateZ(${z}px)
      rotateY(${rotateY}deg)
      rotateZ(${rotateZ}deg)
      scale(${Math.max(scale, 0.7)})
    `;
    };
    const handleDelete = async (id) => {
      const confirmDelete = window.confirm(
        "Are you sure you want to delete this?"
      );
      if (!confirmDelete) return;

      try {
        await deleteDoc(doc(db, "testimonials", id));
        alert("Testimonial deleted!");
      } catch (error) {
        console.error("Error deleting:", error);
        alert("You don't have permission to delete this.");
      }
    };
    const getOpacity = () => {
      if (Math.abs(offset) > 2) return 0;
      return 1 - Math.abs(offset) * 0.3;
    };

    const formatDate = (timestamp) => {
      if (!timestamp) return "Just now";
      const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
      const now = new Date();
      const diffMs = now - date;
      const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));

      if (diffDays === 0) return "Today";
      if (diffDays === 1) return "Yesterday";
      if (diffDays < 7) return `${diffDays} days ago`;
      if (diffDays < 30) return `${Math.floor(diffDays / 7)} weeks ago`;
      return date.toLocaleDateString("en-US", {
        month: "short",
        day: "numeric",
      });
    };

    return (
      <div
        className={`tcard ${isActive ? "tcard-active" : ""}`}
        style={{
          transform: getTransform(),
          opacity: getOpacity(),
          zIndex: 10 - Math.abs(offset),
          filter: isActive ? "none" : `blur(${Math.abs(offset) * 1}px)`,
        }}
      >
        <div className="tcard-glow"></div>
        {/* üî¥ ADD THIS DELETE BUTTON BLOCK */}
        {isAdmin && (
          <button
            className="tcard-delete-btn"
            onClick={(e) => {
              e.stopPropagation(); // Stop the click from dragging the card
              onDelete(testimonial.id);
            }}
            title="Delete (Admin Only)"
          >
            üóëÔ∏è
          </button>
        )}

        <div className="tcard-glow"></div>
        <div className="tcard-content">
          <div className="tcard-quote">"</div>

          {/* Rating Stars */}
          <div className="tcard-stars">
            {[...Array(testimonial.rating || 5)].map((_, i) => (
              <span
                key={i}
                className="tcard-star"
                style={{ animationDelay: `${i * 0.1}s` }}
              >
                ‚òÖ
              </span>
            ))}
          </div>

          {/* Testimonial Text */}
          <p className="tcard-text">{testimonial.text}</p>

          {/* Author */}
          <div className="tcard-author">
            <div
              className="tcard-avatar"
              style={{
                background: testimonial.isAnonymous
                  ? `linear-gradient(135deg, ${
                      testimonial.avatarColor || "#667eea"
                    }, ${testimonial.avatarColor || "#764ba2"}88)`
                  : undefined,
              }}
            >
              {testimonial.isAnonymous ? (
                <span className="anonymous-avatar">{testimonial.avatar}</span>
              ) : (
                <>
                  <img src={testimonial.image} alt={testimonial.name} />
                  <div className="tcard-avatar-ring"></div>
                </>
              )}
            </div>
            <div className="tcard-author-info">
              <h4>
                {testimonial.isAnonymous ? "Anonymous" : testimonial.name}
              </h4>
              <p>
                {testimonial.role || "Visitor"}
                {testimonial.company && (
                  <span className="tcard-company">@ {testimonial.company}</span>
                )}
              </p>
              <span className="tcard-date">
                {formatDate(testimonial.createdAt)}
              </span>
            </div>
            {testimonial.verified && (
              <span className="verified-badge" title="Verified">
                ‚úì
              </span>
            )}
          </div>
        </div>

        <div className="tcard-reflection"></div>
      </div>
    );
  }
);

// ‚úÖ Testimonial Form Modal
const TestimonialFormModal = memo(
  ({ isOpen, onClose, onSubmit, isSubmitting }) => {
    const [formData, setFormData] = useState({
      name: "",
      role: "",
      company: "",
      text: "",
      rating: 5,
      isAnonymous: true,
    });
    const [charCount, setCharCount] = useState(0);
    const maxChars = 280;

    const handleChange = (e) => {
      const { name, value, type, checked } = e.target;
      if (name === "text") {
        if (value.length <= maxChars) {
          setFormData((prev) => ({ ...prev, [name]: value }));
          setCharCount(value.length);
        }
      } else {
        setFormData((prev) => ({
          ...prev,
          [name]: type === "checkbox" ? checked : value,
        }));
      }
    };

    const handleSubmit = (e) => {
      e.preventDefault();
      if (formData.text.trim().length < 10) {
        alert("Please write at least 10 characters");
        return;
      }
      onSubmit({
        ...formData,
        avatar: getRandomAvatar(),
        avatarColor: getRandomColor(),
      });
    };

    const handleBackdropClick = (e) => {
      if (e.target.className === "modal-backdrop") {
        onClose();
      }
    };

    if (!isOpen) return null;

    return (
      <div className="modal-backdrop" onClick={handleBackdropClick}>
        <div className="testimonial-modal">
          <button className="modal-close" onClick={onClose}>
            √ó
          </button>

          <div className="modal-header">
            <span className="modal-icon">üí¨</span>
            <h2>Share Your Experience</h2>
            <p>Your feedback helps others and means a lot to me!</p>
          </div>

          <form onSubmit={handleSubmit} className="testimonial-form">
            {/* Anonymous Toggle */}
            <div className="form-toggle-group">
              <label className="toggle-label">
                <input
                  type="checkbox"
                  name="isAnonymous"
                  checked={formData.isAnonymous}
                  onChange={handleChange}
                />
                <span className="toggle-slider"></span>
                <span className="toggle-text">
                  {formData.isAnonymous ? "üé≠ Anonymous" : "üë§ Show my name"}
                </span>
              </label>
            </div>

            {/* Name & Details (if not anonymous) */}
            {!formData.isAnonymous && (
              <div className="form-row">
                <div className="form-group">
                  <label>Your Name</label>
                  <input
                    type="text"
                    name="name"
                    placeholder="John Doe"
                    value={formData.name}
                    onChange={handleChange}
                    required={!formData.isAnonymous}
                  />
                </div>
                <div className="form-group">
                  <label>Role (optional)</label>
                  <input
                    type="text"
                    name="role"
                    placeholder="Software Engineer"
                    value={formData.role}
                    onChange={handleChange}
                  />
                </div>
              </div>
            )}

            {!formData.isAnonymous && (
              <div className="form-group">
                <label>Company (optional)</label>
                <input
                  type="text"
                  name="company"
                  placeholder="Google, Microsoft, etc."
                  value={formData.company}
                  onChange={handleChange}
                />
              </div>
            )}

            {/* Rating */}
            <div className="form-group">
              <label>Rating</label>
              <div className="rating-input">
                {[1, 2, 3, 4, 5].map((star) => (
                  <button
                    key={star}
                    type="button"
                    className={`rating-star ${
                      formData.rating >= star ? "active" : ""
                    }`}
                    onClick={() =>
                      setFormData((prev) => ({ ...prev, rating: star }))
                    }
                  >
                    ‚òÖ
                  </button>
                ))}
              </div>
            </div>

            {/* Testimonial Text */}
            <div className="form-group">
              <label>Your Message</label>
              <textarea
                name="text"
                placeholder="Share your experience working with me or reviewing my portfolio..."
                value={formData.text}
                onChange={handleChange}
                rows={4}
                required
              />
              <div className="char-counter">
                <span className={charCount > maxChars * 0.9 ? "warning" : ""}>
                  {charCount}/{maxChars}
                </span>
              </div>
            </div>

            {/* Submit Button */}
            <button
              type="submit"
              className="submit-btn"
              disabled={isSubmitting || formData.text.trim().length < 10}
            >
              {isSubmitting ? (
                <>
                  <span className="spinner"></span>
                  Submitting...
                </>
              ) : (
                <>
                  <span>‚ú®</span>
                  Submit Testimonial
                </>
              )}
            </button>

            <p className="form-note">
              üöÄ Your testimonial will appear immediately!
            </p>
          </form>
        </div>
      </div>
    );
  }
);

// ‚úÖ Main TestimonialCards Component
const TestimonialCards = ({ theme }) => {
  const [testimonials, setTestimonials] = useState([]);
  const [activeIndex, setActiveIndex] = useState(0);
  const [isDragging, setIsDragging] = useState(false);
  const [currentDragX, setCurrentDragX] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false); // <--- Add this state
  const containerRef = useRef(null);
  const startX = useRef(0);
  const currentX = useRef(0);
  const lastX = useRef(0);
  const velocity = useRef(0);
  const lastTime = useRef(Date.now());
  const autoPlayTimer = useRef(null);
  // ‚úÖ Check if User is Admin
  useEffect(() => {
    const auth = getAuth();
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user && user.email === "riderogue971@gmail.com") {
        setIsAdmin(true);
      } else {
        setIsAdmin(false);
      }
    });
    return () => unsubscribe();
  }, []);
  // ‚úÖ Firestore Real-Time Listener
  useEffect(() => {
    const testimonialsRef = collection(db, "testimonials");
    // We keep the filter 'approved == true' so you can hide spam manually via Firebase Console later if needed.
    // Since we now save new ones as 'approved: true', they will show up instantly.
    const q = query(
      testimonialsRef,
      where("approved", "==", true),
      orderBy("createdAt", "desc"),
      limit(20)
    );

    const unsubscribe = onSnapshot(
      q,
      (snapshot) => {
        const newTestimonials = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        setTestimonials(newTestimonials);
        setIsLoading(false);
      },
      (error) => {
        console.error("Error fetching testimonials:", error);
        setIsLoading(false);
        setTestimonials(defaultTestimonials);
      }
    );

    return () => unsubscribe();
  }, []);

  // Default testimonials (fallback)
  const defaultTestimonials = [
    {
      id: "default-1",
      name: "Anonymous",
      isAnonymous: true,
      avatar: "ü¶ä",
      avatarColor: "#667eea",
      text: "Amazing portfolio! The attention to detail is impressive.",
      rating: 5,
      createdAt: new Date(),
    },
    {
      id: "default-2",
      name: "Anonymous",
      isAnonymous: true,
      avatar: "üêº",
      avatarColor: "#764ba2",
      text: "Great work! Love the projects and the clean design.",
      rating: 5,
      createdAt: new Date(),
    },
  ];

  const displayTestimonials =
    testimonials.length > 0 ? testimonials : defaultTestimonials;
  // ‚úÖ Handle Delete (Admin Only)
  const handleDelete = useCallback(async (id) => {
    if (!window.confirm("Are you sure you want to delete this testimonial?"))
      return;

    try {
      await deleteDoc(doc(db, "testimonials", id));
      alert("Testimonial deleted successfully!");
    } catch (error) {
      console.error("Error deleting:", error);
      alert("Error deleting. Make sure you are logged in as admin.");
    }
  }, []);
  // ‚úÖ Submit Testimonial to Firestore (UPDATED)
  const handleSubmitTestimonial = async (data) => {
    setIsSubmitting(true);

    try {
      await addDoc(collection(db, "testimonials"), {
        ...data,
        approved: true, // üü¢ NOW LIVE INSTANTLY
        createdAt: serverTimestamp(),
        verified: false,
      });

      setIsModalOpen(false);
      alert("Thank you! Your testimonial is Live! üéâ"); // üü¢ UPDATED MESSAGE
    } catch (error) {
      console.error("Error submitting testimonial:", error);
      alert("Something went wrong. Please try again.");
    } finally {
      setIsSubmitting(false);
    }
  };

  // Auto-play
  useEffect(() => {
    if (displayTestimonials.length <= 1) return;

    startAutoPlay();
    return () => stopAutoPlay();
  }, [activeIndex, displayTestimonials.length]);

  const startAutoPlay = () => {
    stopAutoPlay();
    autoPlayTimer.current = setTimeout(() => {
      goToNext();
    }, 6000);
  };

  const stopAutoPlay = () => {
    if (autoPlayTimer.current) {
      clearTimeout(autoPlayTimer.current);
    }
  };

  const goToNext = () => {
    setActiveIndex((prev) => (prev + 1) % displayTestimonials.length);
  };

  const goToPrev = () => {
    setActiveIndex(
      (prev) =>
        (prev - 1 + displayTestimonials.length) % displayTestimonials.length
    );
  };

  const goTo = (index) => {
    setActiveIndex(index);
  };

  // Touch/Mouse handlers
  const handleStart = (clientX) => {
    stopAutoPlay();
    setIsDragging(true);
    startX.current = clientX;
    currentX.current = clientX;
    lastX.current = clientX;
    lastTime.current = Date.now();
    velocity.current = 0;
  };

  const handleMove = (clientX) => {
    if (!isDragging) return;

    const now = Date.now();
    const dt = now - lastTime.current;

    if (dt > 0) {
      const dx = clientX - lastX.current;
      velocity.current = (dx / dt) * 16;
    }

    currentX.current = clientX;
    lastX.current = clientX;
    lastTime.current = now;

    const diff = currentX.current - startX.current;
    setCurrentDragX(diff);
  };

  const handleEnd = () => {
    if (!isDragging) return;

    const diff = currentX.current - startX.current;
    const threshold = 60;

    if (diff < -threshold || velocity.current < -2) {
      goToNext();
    } else if (diff > threshold || velocity.current > 2) {
      goToPrev();
    }

    setCurrentDragX(0);
    setIsDragging(false);
    startAutoPlay();
  };

  // Mouse events
  const onMouseDown = (e) => handleStart(e.clientX);
  const onMouseMove = (e) => handleMove(e.clientX);
  const onMouseUp = () => handleEnd();
  const onMouseLeave = () => {
    if (isDragging) handleEnd();
  };

  // Touch events
  const onTouchStart = (e) => handleStart(e.touches[0].clientX);
  const onTouchMove = (e) => handleMove(e.touches[0].clientX);
  const onTouchEnd = () => handleEnd();

  return (
    <div className="testimonials-wrapper">
      {/* Header */}
      <div className="testimonials-header">
        <span className="testimonials-badge">
          <span className="live-indicator"></span>
          LIVE TESTIMONIALS
        </span>
        <h2 className="testimonials-title">
          What People <span className="gradient-text">Say About Me</span>
        </h2>
        <p className="testimonials-subtitle">
          Real feedback from amazing people ‚Ä¢ Updated in real-time
        </p>
      </div>

      {/* Loading State */}
      {isLoading ? (
        <div className="testimonials-loading">
          <div className="loading-spinner"></div>
          <p>Loading testimonials...</p>
        </div>
      ) : (
        <>
          {/* Carousel */}
          <div
            ref={containerRef}
            className={`testimonials-carousel ${
              isDragging ? "is-dragging" : ""
            }`}
            onMouseDown={onMouseDown}
            onMouseMove={onMouseMove}
            onMouseUp={onMouseUp}
            onMouseLeave={onMouseLeave}
            onTouchStart={onTouchStart}
            onTouchMove={onTouchMove}
            onTouchEnd={onTouchEnd}
          >
            <div className="testimonials-track">
              {displayTestimonials.map((testimonial, index) => (
                <TestimonialCard
                  key={testimonial.id}
                  testimonial={testimonial}
                  index={index}
                  activeIndex={activeIndex}
                  dragX={currentDragX}
                  isDragging={isDragging}
                  isAdmin={isAdmin} // <--- Pass isAdmin prop
                  onDelete={handleDelete} // <--- Pass onDelete prop
                />
              ))}
            </div>
          </div>

          {/* Navigation */}
          <div className="testimonials-nav">
            <button
              className="nav-arrow nav-prev"
              onClick={goToPrev}
              aria-label="Previous"
            >
              <svg
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
              >
                <path d="M15 18l-6-6 6-6" />
              </svg>
            </button>

            <div className="nav-dots">
              {displayTestimonials.slice(0, 8).map((_, index) => (
                <button
                  key={index}
                  className={`nav-dot ${index === activeIndex ? "active" : ""}`}
                  onClick={() => goTo(index)}
                  aria-label={`Go to testimonial ${index + 1}`}
                >
                  <span className="dot-progress"></span>
                </button>
              ))}
              {displayTestimonials.length > 8 && (
                <span className="more-dots">
                  +{displayTestimonials.length - 8}
                </span>
              )}
            </div>

            <button
              className="nav-arrow nav-next"
              onClick={goToNext}
              aria-label="Next"
            >
              <svg
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
              >
                <path d="M9 18l6-6-6-6" />
              </svg>
            </button>
          </div>

          {/* Testimonial Count */}
          <div className="testimonials-count">
            <span className="count-number">{displayTestimonials.length}</span>
            <span className="count-label">testimonials</span>
          </div>
        </>
      )}

      {/* Add Testimonial Button */}
      <button
        className="add-testimonial-btn"
        onClick={() => setIsModalOpen(true)}
      >
        <span className="btn-icon">‚úçÔ∏è</span>
        <span className="btn-text">Leave a Testimonial</span>
        <span className="btn-glow"></span>
      </button>

      {/* Modal */}
      <TestimonialFormModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSubmit={handleSubmitTestimonial}
        isSubmitting={isSubmitting}
      />
    </div>
  );
};

export default memo(TestimonialCards);
